import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"

function freeCashFlowPerShareTtm(tableName: string) {
  const title = "FCF/Share (LTM)"
  return genericColumn({
    endpoint: "companyRatios",
    columnKeys: ["freeCashFlowPerShareTtm"],
    size: 110,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Free cash flow per share.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyRatios}
          onClick={() => {
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} recently reported about free cash flow per share?`,
              value: info.row.original.financialData.companyRatios?.data?.freeCashFlowPerShareTtm,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={info.row.original.financialData.companyRatios?.data?.freeCashFlowPerShareTtm}
        />
      )
    },
  })
}

export { freeCashFlowPerShareTtm }
